#services {
    padding: 100px 0 75px; }

.services-container {
    width: 1000px;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: auto; }

.service-item {
    width: 33.33%;
    padding: 0 15px;
    margin-bottom: 30px; }

.service-img-wrap {
    width: 100%;
    height: 100%;
    padding: 50%;
    position: relative;
    overflow: hidden;
    border: 1px solid $top-bdr-color;
    border-radius: 5px;
    cursor: pointer;
    &:hover .service-desc {
        opacity: 1;
        transition: opacity .4s; } }

.service-desc {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
    z-index: 5;
    opacity: 0;
    transition: opacity .4s;
    color: #fff;
    font-size: 24px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    flex-flow: column wrap;
    padding: 15px; }

.service-desc-text {
    width: 100%; }

.more-link {
    font-size: 18px;
    color: #d1d1d1;
    transition: color .4s;
    text-transform: uppercase;
    &:hover {
        text-decoration: none;
        color: #fff;
        transition: color .4s; } }


.service-img {
    position: absolute;
    top: -9999px;
    left: -9999px;
    right: -9999px;
    bottom: -9999px;
    width: 100%;
    margin: auto; }
