#delivery {
    padding: 70px 0; }

.delivery-container {
    border-top: 1px solid $top-bdr-color;
    display: flex; }

.delivery-method:nth-child(1) {
    border-right: 1px solid $top-bdr-color; }

.delivery-method {
    width: 50%;
    padding: 0 30px 20px; }

.delivery-title {
    font-size: 22px;
    text-align: center;
    margin: 20px 0; }

.delivery-img-wrap {
    width: 100%;
    position: relative;
    padding-top: 54.9%;
    overflow: hidden;
    border-radius: 5px;
    margin-bottom: 30px;
    box-shadow: 0px 5px 10px 0px rgba(50, 50, 50, 0.5); }

.delivery-img {
    position: absolute;
    margin: auto;
    top: -9999px;
    bottom: -9999px;
    left: -9999px;
    right: -9999px;
    width: 100%; }
