#advantages {
    background-color: #f9f9f9;
    padding: 80px 0 50px; }

.advantage-item {
    width: 33.33%;
    display: flex; }

.advantages-list {
    display: flex;
    flex-wrap: wrap; }

.advantage-item {
    width: 33.33%;
    margin-bottom: 30px;
    padding: 30px;
    align-items: center; }

.advantage-icon {
    min-width: 60px;
    width: 60px;
    min-height: 60px;
    height: 60px;
    margin-right: 20px; }

.advantage-caption {
    font-weight: bold;
    font-size: 18px; }

.advantage-icon {
    fill: $main-color; }
