.iziModal {
    max-width: calc(100% - 30px) !important;
    max-height: calc(100% - 30px) !important; }

#feedback-modal {
    width: 400px; }

#services-modal {
    width: 800px; }

.feedback-content {
    padding: 30px 30px 50px; }

.modal-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center; }

.feedback-form {
    display: flex;
    flex-flow: column wrap;
    justify-content: center; }

.feedback-input {
    margin-bottom: 20px;
    padding: 0 18px;
    height: 46px;
    border-radius: 5px;
    border: 1px solid $top-bdr-color;
    &:focus {
        outline: 0;
        border: 1px solid $main-color; } }

.feedback-submit {
    margin: 0; }

.close-icon {
    position: absolute;
    right: 15px;
    top: 15px;
    width: 20px;
    height: 20px;
    cursor: pointer;
    fill: $main-text-color;
    transition: fill .4s;
    &:hover {
        fill: $main-color;
        transition: fill .4s; } }

.service-modal-content {
    padding: 30px 50px;
    .feedback-btn {
        padding: 10px 40px;
        display: block;
        margin: 20px auto 10px; } }

.service-modal-desc p {
    margin-bottom: 15px; }
