@media (max-width: 1400px) {
    .slider-container {
        padding: 0 150px; } }

@media (max-width: 1023px) {
    .about-content {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center; }
    .about-left,
    .about-text,
    .cert-img {
        width: 100%;
        height: auto; }
    .about-imp-wrap {
        margin: 0 0 30px; }
    .blockquote {
        margin-bottom: 50px; }
    .top-header-wrap {
        position: fixed;
        z-index: 30;
        top: 0;
        left: 0;
        background-color: #fff; }

    .slider-item {
        padding-top: 60%;
        height: auto; }

    .top-nav-wrap {
        position: fixed;
        top: 40px;
        height: calc(100% - 40px);
        left: 0;
        z-index: 20;
        background-color: #fff;
        transform: translateY(-100%);
        transition: all .3s;
        box-shadow: 0px 5px 10px 0px rgba(50, 50, 50, 0.5);
        overflow: auto; }

    .top-nav-wrap.active {
        transform: translateY(0); }
    .burger {
        display: block;
        &.active {
            &:before {
                transform: rotate(45deg);
                top: 9px; }
            &:after {
                transform: rotate(-45deg);
                bottom: 9px; }
            span {
                transform: scale(0); } } }
    body.lock {
        overflow: hidden; }

    .top-nav,
    .nav-right,
    .menu {
        align-items: center;
        flex-direction: column; }

    .menu-item {
        margin: 5px 0; }

    .menu {
        margin: 10px 0; }

    .top-nav {
        padding: 20px 0;
        overflow: auto; }

    .slider-wrap {
        margin-top: 40px; }

    .phone {
        border-right: 1px solid $top-bdr-color;
        height: 100%;
        padding-right: 15px;
        display: flex;
        align-items: center; } }


@media (max-width: 900px) {
    .advantage-item {
        width: 50%; } }

@media (max-width: 767px) {
    .service-item {
        width: 50%; }
    .advantage-item {
        width: 100%; }
    .delivery-container {
        flex-direction: column; }
    .delivery-method {
        width: 100%; }
    .delivery-method:nth-child(1) {
        border-right: none;
        border-bottom: 1px solid $top-bdr-color; }
    .footer-contacts {
        flex-direction: column;
        li {
            margin: 10px 0;
            text-align: center; } }
    .slider-container {
        padding: 0 60px; }
    .slider-icon {
        width: 50px; }
    .left-content .time {
        display: none; }
    .left-content .mail {
        padding: 0 8px 0 0; }

    .menu-contacts {
        display: flex;
        .mail {
            display: none; }
        .time {
            border: none; } }

    .slider-text {
        font-size: 32px;
        margin-bottom: 0px;
        line-height: 130%; }
    p.slider-text {
        font-size: 20px; }
    .section-title {
        font-size: 24px; }
    .more-link {
        font-size: 16px; }
    .service-desc-text {
        font-size: 20px; } }

@media (max-width: 425px) {
    .blockquote {
        margin-bottom: 30px;
        font-size: 13px; }
    .about-text p {
        font-size: 15px; }
    .slider-icon {
        height: 20px; }
    .service-item {
        width: 100%; }
    .left-content {
        display: none; }
    .phone {
        width: 100%; }
    .right-content {
        width: 100%;
        justify-content: space-between; }
    .advantage-icon {
        min-width: 50px;
        width: 50px; }

    .menu-contacts {
        .mail {
            display: block; }
        .time {
            border-right: 1px solid $top-bdr-color; } }
    .service-modal-desc p {
        font-size: 14px; }
    .service-modal-content {
        padding: 30px; } }

@media (max-width: 375px) {
    .footer-contacts span {
        display: block; }
    .feedback-content {
        padding: 30px 20px 40px; }
    .modal-title {
        font-size: 20px; }
    .service-modal-content {
        padding: 30px 15px; }
    .about-img-desc {
        padding: 5px;
        font-size: 14px; } }
