.slider-item {
    height: 600px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }

.slider-wrap {
    position: relative; }

.slider-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.slider-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    position: relative;
    z-index: 2;
    pointer-events: none; }

.slider-text {
    color: #fff;
    font-size: 48px; }

p.slider-text {
    font-size: 26px; }

.slider-icon {
    height: 30px;
    width: 100px;
    fill: #fff; }

.slider {
    .owl-dots {
        display: none; }
    .owl-nav {
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: space-between; }
    .owl-prev,
    .owl-next {
        height: 100%;
        opacity: .4;
        transition: opacity .3s;
        &:hover {
            opacity: .9;
            transition: opacity .3s; }
        &:focus {
            outline: 0; } } }

