@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";

$top-color: #777;
$top-bdr-color: #c5cbcf;
$main-color: #17a7dd;
$main-text-color: #333;

* {
    box-sizing: border-box; }

html {
    scroll-behavior: smooth; }


body, body a {
    color: $main-text-color;
    font-family: 'Roboto', sans-serif; }

p {
    margin: 0; }

ul {
    margin: 0;
    padding: 0;
    list-style: none; }

.container {
    width: 1200px;
    max-width: 100%;
    margin: auto;
    padding: 0 15px; }

.section-title {
    text-align: center;
    margin-bottom: 40px;
    font-size: 28px;
    text-transform: uppercase;
    padding: 0 15px; }

.feedback-btn {
    color: $main-text-color;
    border: 1px solid $top-bdr-color;
    background: none;
    margin-left: 15px;
    padding: 10px 18px;
    transition: color .3s;
    &:hover {
        border: 1px solid $main-color;
        color: $main-color;
        transition: color .3s; }
    &:focus {
        outline: 0; } }

@import "owlcarousel/owl.carousel.min.css";
@import "iziModal.min.css";
