.about {
    padding-top: 70px; }

.about-content {
    display: flex; }

.about-img {
    width: 100%;
    height: auto; }

.about-left {
    width: 40%; }

.about-imp-wrap {
    margin: 0 30px 30px 0;
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 0px 5px 10px 0px rgba(50, 50, 50, 0.5); }

.about-img-desc {
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    padding: 5px 10px;
    span {
        display: block; } }

.about-text {
    width: 60%; }

.about-text {
    font-size: 18px;
    p {
        margin-bottom: 15px; } }

.blockquote {
    font-style: italic;
    text-align: center;
    padding: 0 30px; }

.certificates {
    padding: 70px 0; }

.cert-container {
    display: flex;
    flex-wrap: wrap; }

.cert-img {
    width: 50%;
    padding: 15px;
    box-shadow: 0px 5px 10px 0px rgba(50, 50, 50, 0.5);
    margin-bottom: 30px; }
