.top-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    color: $top-color; }

.left-content,
.right-content {
    display: flex;
    height: 100%;
    align-items: center; }

.time {
    border-right: 1px solid $top-bdr-color;
    display: flex;
    align-items: center;
    padding: 0 8px;
    height: 100%; }

.top-icon {
    width: 15px;
    height: 15px;
    margin-right: 5px;
    fill: $top-color; }

.mail {
    padding: 0 8px; }


.phone a {
    color: $top-color;
    font-size: 16px;
    &:hover {
        color: $top-color;
        text-decoration: none; } }


.top-header-wrap {
    border-bottom: 1px solid $top-bdr-color;
    width: 100%; }

.logo img {
    height: 120px; }

.top-nav-wrap {
    border-bottom: 2px solid $main-color;
    padding: 5px 0;
    width: 100%; }

.top-nav {
    display: flex;
    justify-content: space-between; }

.nav-right {
    display: flex;
    align-items: center; }

.menu {
    display: flex;
    font-size: 16px;
    text-transform: uppercase; }

.menu-item a {
    transition: color .3s;
    padding: 15px 10px;
    &:hover {
        color: $main-color;
        transition: color .3s;
        text-decoration: none; } }

.burger {
    height: 20px;
    width: 30px;
    position: relative;
    margin-left: 15px;
    display: none;
    cursor: pointer;
    &:before, &:after {
        content: '';
        position: absolute;
        background-color: $top-color;
        height: 2px;
        width: 100%;
        left: 0;
        transition: all .3s; }
    &:before {
        top: 0; }
    &:after {
        bottom: 0; }
    span {
        transition: all .3s;
        position: absolute;
        background-color: $top-color;
        height: 2px;
        width: 100%;
        top: 50%;
        left: 0;
        transform: translateY(-50%); } }

.menu-contacts {
    display: none;
    margin: 20px 0; }

